import React, { useState, useEffect } from 'react';
import img1 from './photos/img1.jpg';
import { Card, Button } from 'react-bootstrap';
import axios from 'axios';

function useEndpoint(req) {
    const [res, setRes] = useState({
      data: null,
      complete: false,
      pending: false,
      error: false
    });
    useEffect(
      () => {
        setRes({
          data: null,
          pending: true,
          error: false,
          complete: false
        });
        axios(req)
          .then(res =>
            setRes({
              data: res.data,
              pending: false,
              error: false,
              complete: true
            })
          )
          .catch(() =>
            setRes({
              data: null,
              pending: false,
              error: true,
              complete: true
            })
          );
      },
      [req.url]
    );
    return res;
  }

